<template>
    <div>
      <div class="pt-md-5">
        <b-toast id="my-toast" variant="warning">
          <template v-slot:toast-title>
            <div class="ml-auto text-center">
              <b-img blank blank-color="#ff5555" class="" width="12" height="12"></b-img>
              <strong class="ml-1">Error!!</strong>
            </div>
          </template>
          <p class="text-center">
            <b-spinner type="grow" small class="mr-3"></b-spinner>{{errorMessage}}
            <b-spinner type="grow" small class="ml-3"></b-spinner>
          </p>
        </b-toast>
        <div class="mx-auto text-center">
            <p class="text-center mt-4 mt-md-0" style="font-weight: bolder;"><b-link to="/login">Login</b-link> / <b-link href="#">Register</b-link></p>
        </div>
        <div class="mx-auto mb-5 shadow-none bg-light text-center"  style="width:90%;max-width: 600px;">
          <div class="mx-auto mt-4 text-center" style="width:90%;max-width: 400px;">
            <b-form  @submit.prevent="submitOnAction">
                <b-form-group
                    id="input-group-6"
                    label="Name:"
                    label-for="input-6"
                    description=""
                >
                  <b-input-group>
                    <b-input-group-prepend is-text>
                      <b-icon icon="person-fill"></b-icon>
                    </b-input-group-prepend>
                    <b-form-input
                        id="input-6"
                        type="text"
                        :required="false"
                        placeholder=""
                        v-model="name"
                    ></b-form-input>
                  </b-input-group>
                </b-form-group>
                <b-form-group
                    id="input-group-1"
                    label="Email address:"
                    label-for="input-1"
                    description="We'll never share your email with anyone else."
                >
                  <b-input-group>
                    <b-input-group-prepend is-text>
                      <b-icon icon="envelope-fill"></b-icon>
                    </b-input-group-prepend>
                    <b-form-input
                        id="input-1"
                        type="email"
                        :required="false"
                        placeholder="Enter email"
                        v-model="email"
                    ></b-form-input>
                  </b-input-group>
                </b-form-group>
                <b-form-group
                    id="input-group-2"
                    label="Phone Number:"
                    label-for="input-2"
                >
                  <b-input-group>
                    <b-input-group-prepend is-text>
                      <span class="glyphicon glyphicon-earphone"></span>
                      <b-icon icon="tel"></b-icon>
                    </b-input-group-prepend>
                    <b-form-input
                        class=""
                        id="input-2"
                        type="tel"
                        required
                        v-model="phoneNo"
                    ></b-form-input>
                  </b-input-group>
                </b-form-group>
                <b-form-group
                    id="input-group-3"
                    label="Password:"
                    label-for="input-3"
                >
                  <b-input-group>
                    <b-form-input
                        class=""
                        id="input-3"
                        :type="selectIcon?'password':'text'"
                        required
                        v-model="password"
                    ></b-form-input>
                    <b-input-group-append is-text>
                      <b-icon :icon="selectIcon?'eye-slash-fill':'eye-fill'" @click="selectIcon=!selectIcon"></b-icon>
                    </b-input-group-append>
                  </b-input-group>
                </b-form-group>
                <b-form-group
                    id="input-group-4"
                    label="Confirm Password:"
                    label-for="input-4"
                >
                  <b-input-group>
                    <b-form-input
                        class=""
                        id="input-4"
                        :type="selectIcon1?'password':'text'"
                        required
                        v-model="confirmPassword"
                    ></b-form-input>
                    <b-input-group-append is-text>
                      <b-icon :icon="selectIcon1?'eye-slash-fill':'eye-fill'" @click="selectIcon1=!selectIcon1"></b-icon>
                    </b-input-group-append>
                  </b-input-group>
                </b-form-group>
                <p class="text-md-center mt-n2" v-if="submitted&&password!=confirmPassword" style="font-weight: lighter;color:red;">
                   Password doesn't match!!</p>
                <b-form-group
                    id="input-group-5"
                    label="Delivery Address:"
                    label-for="input-5"
                >
                  <b-input-group>
                    <b-input-group-prepend is-text>
                      <b-icon icon="geo-alt"></b-icon>
                    </b-input-group-prepend>
                    <b-form-input
                        class=""
                        id="input-5"
                        type="text"
                        v-model="address"
                    ></b-form-input>
                  </b-input-group>
                </b-form-group>
                <p class="text-md-center" v-if="existedAccount" style="font-weight: lighter;color:red;">
                Already have an account</p>
                <div class="d-flex justify-content-center">
                  <b-button  ref="submit" type="submit" variant="primary">
                    Sign Up</b-button>
                </div>
                <b-overlay :show="busy" no-wrap @shown="onShown" @hidden="onHidden">
                  <template v-slot:overlay>
                    <div v-if="processing" class="text-center p-4 bg-primary text-light rounded">
                      <b-icon icon="cloud-upload" font-scale="4"></b-icon>
                      <div class="mb-3">Processing...</div>
                      <b-progress
                        min="1"
                        max="20"
                        :value="counter"
                        variant="success"
                        height="3px"
                        class="mx-n4 rounded-0"
                      ></b-progress>
                    </div>
                    <div
                      v-else
                      ref="dialog"
                      tabindex="-1"
                      role="dialog"
                      aria-modal="false"
                      aria-labelledby="form-confirm-label"
                      class="text-center p-3"
                    >
                      <p><strong id="form-confirm-label">Are you sure?</strong></p>
                      <div class="d-flex">
                        <b-button variant="outline-danger" class="mr-3" @click="onCancel">
                          Cancel
                        </b-button>
                        <b-button variant="outline-success" @click="onOK">OK</b-button>
                      </div>
                    </div>
                  </template>
                </b-overlay>
            </b-form>
          </div>
        </div>
      </div>
    </div>
</template>
<script>
import { mapActions } from 'vuex'
import store from '../../../store'

export default {
  name: 'SignUp',
  data () {
    return {
      selectIcon: true,
      selectIcon1: true,
      submitted: false,
      name: null,
      email: null,
      phoneNo: null,
      password: null,
      mismatchPassword: false,
      confirmPassword: null,
      address: null,
      existedAccount: false,
      errorMessage: '',
      busy: false,
      processing: false,
      counter: 1
    }
  },
  methods: {
    ...mapActions([
      'signUp'
    ]),
    submitOnAction () {
      this.submitted = true
      if (this.password !== this.confirmPassword) {
        this.mismatchPassword = true
        return
      }
      this.busy = true
      this.processing = false
    },
    showMessage () {
      this.$bvToast.show('my-toast')
    },
    onShown () {
      this.$refs.dialog.focus()
    },
    onHidden () {
      this.$refs.submit.focus()
    },
    onCancel () {
      this.busy = false
    },
    onOK () {
      this.processing = true
      // evt.preventDefault()
      if (this.counter < 20) {
        while (this.counter < 20) {
          this.counter++
        }
      }
      this.signUp({
        phoneNo: this.phoneNo,
        name: this.name,
        email: this.email,
        address: this.address,
        password: this.password
      })
        .then(response => {
          this.$router.push({ name: 'Home' })
        })
        .catch(error => {
          this.processing = false
          this.busy = false
          this.errorMessage = error.response.data.error
          this.showMessage()
          console.log('sorry: ' + error.response.data.error)
        })
    }
  },
  beforeRouteEnter (to, from, next) {
    if (!store.getters.authToken) {
      next()
    } else {
      next({ name: 'UserProfile' })
    }
  }
}
</script>
